body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:  #FFB6C1;
  
 
}
.container{
  display: flex;
  width: 100%;
  align-items: center;
 justify-content: center;
  flex-wrap: wrap;
}

.container .logo img{
  width: 50px;
  height: 50px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.save{
  border-radius: 20%;
  background-color: rgb(243, 240, 53);
  display: inline-block;
  margin: 8px auto;
  color: rgb(2, 2, 2);
  font-size: 0.75rem;
  padding: 0.25em 0.7em;
  border-radius: 4px;
  text-align: center;
  position: relative;
  cursor: pointer;
  appearance: none;
  border: 0;
  transition: border-readius linear 0.05s,width linear 0.05s;
}
h1{
  text-align: center;
  font-family: Neucha;
  font-size: 2.2rem;
}
.sec_part{
  background-color:rgb(255, 105, 180,.6);
  /* background-color:#FFC0CB; */
  border-radius: 2%;
  padding: 0.4rem 0.4rem 2.4rem 0.4rem;
  font-family:Neucha;
  font-size: 1.15rem;

}
.nambtn_area{
  /* background-color: green; */
  width: 10rem;
 
}
.numbtn_area{
  /* background-color: green; */
  width: 15rem;
  
}
.delbtn_area{
  /* background-color: green; */
  width: 15rem;
}
.input{
 background-color: #FFB6C1;
 border-radius: 12px;
 box-sizing: border-box;
 border:0;
 color: rgb(12, 12, 12);
 outline: none;
 padding: 4px 20px;
 margin-left: 0.75rem;
 font-size:1rem;

}
.inputf{
  background-color: #f8e9e9;
  border-radius: 5px;
  box-sizing: border-box;
  border:0;
  color: rgb(0, 0, 0);
  outline: none;
  padding: 4px 20px;
  margin-left: 0.75rem;
  font-size:1rem;
  width: 13rem;
 
 }


.error {
  color: rgb(255, 255, 255);
  background: rgb(241, 11, 11);
  font-size: 1rem;
  border-style: solid;
  border-radius: 5px;
  font-family: monospace;
  margin-bottom: 10px;
  padding: 1rem 0rem;
  border: 0;
}
.success {
  color: rgb(0, 0, 0);
  border: 0;
  font-family: monospace;
  background:rgba(131, 147, 242, 0.945);
  font-size: 1rem;
  border-style: solid;
  border-radius: 7px;
  padding: 1rem 0rem;
  margin-bottom: 10px;
}
.delBtn{
  border-radius: 50%;
  background-color: rgb(192, 243, 53);
  display: inline-block;
  margin: 8px auto;
  color: rgb(2, 2, 2);
  font-size: 0.75rem;
  padding: 0.25em 0.7em;
  border-radius: 4px;
  text-align: center;
  position: relative;
  cursor: pointer;
  appearance: none;
  border: 0;
  transition: border-readius linear 0.05s,width linear 0.05s;
}
